import React, { memo } from 'react'
import { useFetch } from '@ttungbmt/react-hooks'
import { ProgressBar } from 'react-bootstrap'
import { InnerHTML } from '@core/components'
import { map } from '@ttungbmt/utils';
import { useDispatch } from '@ttungbmt/redux-toolkit';
import ReactTable from '@core/components/ReactTable'
import { actions as odichActs } from 'app/reducers/odich'

function TabDnc({models}) {
    const dispatch = useDispatch()
    const ids = map(models, 'gid')
    const {isLoading, data} = useFetch({
        url: '/api/odich/tim-dnc',
        method: 'POST',
        data: {cabenhIds: ids}
    })

    if (isLoading || !data) return <ProgressBar className="m-2" animated now={100}/>

    return (
        <ReactTable
            columns={[
                {label: '#', as: 'serial'},
                {label: 'Tên điểm', value: 'ten_cs', width: 175},
                {label: 'Loại hình', value: 'loaihinh'},
            ]}
            data={data.data}
            onRowClick={(e, row) => dispatch(odichActs.showPopDnc(row.original))}
        />
    )
}

export default memo(TabDnc)