import React, {memo} from 'react'
import { Link } from "react-router-dom"
import { Tooltip } from '@core/components'

function BtnTool({title, onClick, icon, href, to, link}) {
    let onBtnClick = (e) => {
        e.stopPropagation()
        onClick && onClick(e)
    }

    let child = <button onClick={onBtnClick}><i className={icon}/></button>
    if(to) child = <Link to={to} onClick={onBtnClick}><i className={icon}/></Link>
    if(href) child = <button onClick={() => window.open(href)}><i className={icon}/></button>

    if(title) return <Tooltip title={title}>{child}</Tooltip>

    return child
}

export default memo(BtnTool)