import React, { memo } from 'react'
import { Row, Col, Form } from 'react-bootstrap'
import { Field } from '@core/components/Form'
import { getters as gt } from "app/Store"
import $ from 'jquery'

import { submitForm } from '@core/components/Form'

function FormSearch({handleSubmit, register, onSubmit}) {
    let dm_quan = gt.getCat('dm_quan'),
        dm_phuong = gt.getCat('dm_phuong')

    const onChange = (e) => {
        if(e.target.name === 'maquan') {
            $('#drop-phuong').on('depdrop:afterChange', function(event, id, value, jqXHR, textStatus) {submitForm()})
        } else {
            submitForm()
        }
    }

    return (
            <Form onSubmit={handleSubmit(onSubmit)}>
                <div style={{padding: '10px 10px 0 10px'}}>
                    <Row>
                        <Col md={6}>
                            <Field
                                id="drop-quan"
                                as="select"
                                label="Quận huyện"
                                name="maquan"
                                placeholder="Chọn quận huyện..."
                                onChange={onChange}
                                options={dm_quan}
                            />
                        </Col>
                        <Col md={6}>
                            <Field
                                id="drop-phuong"
                                as="select"
                                label="Phường xã"
                                name="maphuong"
                                placeholder="Chọn phường xã..."
                                options={dm_phuong}
                                depends={['drop-quan']}
                                onChange={onChange}
                                url={`/api/dm/phuong?role=true`}
                            />
                        </Col>
                    </Row>

                </div>
                <input id="btn-submit-form" type="submit" className="d-none"/>
            </Form>
    )
}

export default memo(FormSearch)