import React, { memo } from 'react'

function Header({headerGroups}) {
    return (
        <thead>
        {headerGroups.map(group => (
            <tr {...group.getHeaderGroupProps()}>
                {group.headers.map(column => (
                    <th {...column.getHeaderProps(headerProps)}>{column.render('Header')}</th>
                ))}
            </tr>
        ))}
        </thead>
    )
}

const headerProps = (props, {column}) => {
    let newStyle = column.style ? column.style : {}

    if (column.minWidth > 0) newStyle.minWidth = column.minWidth
    if (column.width !== 150) newStyle.width = column.width
    if (column.maxWidth < 1000) newStyle.maxWidth = column.maxWidth

    return {
        ...props,
        style: newStyle
    }
}

export default memo(Header)