import React, { memo, Fragment } from 'react'
import cx from "classnames"
import moment from 'app/utils/moment'
import { useTable } from 'react-table'
import { get } from '@ttungbmt/utils'
import { useSelector, useDispatch, useParams } from '@ttungbmt/redux-toolkit'
import { Table } from 'react-bootstrap'
import { selectors as odichSels, actions as odichActs } from 'app/reducers/odich'
import { ProgressBar } from 'react-bootstrap'

const Cell = memo(({getCellProps, render, index}) => {
    if (index === 0) {
        return <td>{render('Cell')}</td>
    }

    return <td>{render('Cell')}</td>
})

function TabCabenh({tableData}) {
    let { id } = useParams()
    const dispatch = useDispatch()
    const odich = useSelector(state => odichSels.getOdich(state, id)) || {}
    let range = get(odich, 'range.value'),
        loading = get(odich, 'loading', false)

    const {
        headers,
        getTableBodyProps,
        rows,
        prepareRow, ...props
    } = useTable(tableData)

    if(!range) return <Fragment />

    return (
        <div className="table-responive">
            <Table className="table-sm" style={{marginTop: -10, minWidth: 600}}>
                <thead>
                <tr>
                    {headers.map(({Header, width, ...prh}, k) => {
                        let style = {}
                        if (width !== 150) style.width = width
                        return <th key={k} style={style}>{Header}</th>
                    })}
                </tr>
                </thead>
                <tbody {...getTableBodyProps()}>
                {loading ? (
                    <tr><td colSpan={headers.length}><ProgressBar className="m-2" animated now={100}/></td></tr>
                ) : rows.map((row, i) => {
                        prepareRow(row);
                        let model = row.original,
                            date = moment(model.ngaymacbenh_nv, 'L').unix()

                        return (
                            <tr {...row.getRowProps()}
                                onClick={() => dispatch(odichActs.panToCabenhCir(model))}
                                className={cx({
                                    'table-warning': model.selected,
                                    'text-strikethrough': date < get(range, '0', 0) || date > get(range, '1', 0)
                                })}

                            >
                                {row.cells.map((c, k) => <Cell key={k} index={k} {...c} />)}
                            </tr>
                        )
                    }
                )}
                </tbody>
            </Table>
        </div>
    )
}

export default memo(TabCabenh)