import React, { memo, useContext } from 'react'
import { Tooltip } from '@core/components'
import { Badge } from 'react-bootstrap'
import { ContainerContext } from "../ContainerProvider";

import { ToolGroup } from "./index"

function NavList({form, name: key, methods, pageQl}) {
    const {actions: acts} = useContext(ContainerContext)

    const refreshData = () => {
        methods.reset()
        acts.refreshData({form})
    }

    const items = [
        {title: 'Đến trang quản lý', icon: 'icon-link2', href: pageQl},
        {title: 'Bộ lọc', icon: 'icon-filter4', onClick: () => acts.setShow(true)},
        {title: 'Tải lại trang', icon: 'icon-reset', onClick: refreshData},
    ]

    return (
        <>
            <ToolGroup items={items}/>
            <div className="ml-auto"><Tooltip title="Trang trước">
                <button onClick={() => acts.prevPage({key})}><i className="icon-square-left"/></button>
            </Tooltip></div>
            <div className="align-self-center pl-1 pr-1">
                <Badge variant="success">{form.page}</Badge>
            </div>
            <div><Tooltip title="Trang tiếp theo">
                <button onClick={() => acts.nextPage({key})}><i className="icon-square-right"/></button>
            </Tooltip></div>
        </>
    )
}

export default memo(NavList)