import React, { memo, useState } from 'react'
import { Tabs, Tab } from 'react-bootstrap'
import { useSelector, useDispatch, useParams } from '@ttungbmt/redux-toolkit'
import { isEmpty, map } from '@ttungbmt/utils'
import { getRangeDate } from "app/utils"
import { Tooltip } from '@core/components'
import { selectors as odichSels, actions as odichActs } from 'app/reducers/odich'
import { selectors as pcdSels } from 'app/reducers/pcd'
import Alert from '@material-ui/lab/Alert'

import { SubTitle } from '../../../components'
import Slider from './Slider'
import TabCabenh from './TabCabenh'
import TabDnc from './TabDnc'
import TabToAh from './TabToAh'

const TAB_CABENH = 'cabenh'
const TAB_TO_AH = 'to_ah'
const TAB_DNC = 'dnc'

function OdichViewPage({SidebarLayout}) {
    const dispatch = useDispatch()
    const {id} = useParams()
    const [key, setKey] = useState(TAB_CABENH)
    let odich = useSelector(state => odichSels.getOdich(state, id)) || {}


    let cabenhs = odich.cabenhs

    const items = [
        {
            title: 'Tìm ổ dịch bán tự động',
            icon: odich.loading ? 'icon-spinner2 spinner' : 'icon-search4',
            onClick: () => dispatch(odichActs.searchSemiAutoOD({id, cabenhs})),
            visible: !isEmpty(cabenhs)
        },
        {
            title: 'Xóa ổ dịch',
            icon: 'icon-bin',
            visible: !isEmpty(cabenhs),
            onClick: () => dispatch(odichActs.remove(id))
        },
        {
            className: 'ml-auto',
            title: 'Lưu ổ dịch',
            icon: 'icon-file-plus',
            onClick: () => {
                let cabenhIds = map(cabenhs, 'gid').join(',')

                if(odich.odich_id){
                    window.open(`/sxh/odich/update?id=${odich.odich_id}&cabenh_ids=`+cabenhIds)
                } else {
                    window.open(`/sxh/odich/create?cabenh_ids=`+cabenhIds)
                }
            },
        },
    ]

    const tableData = {
        columns: [
            {Header: '#', accessor: (d, index) => index + 1, width: 55},
            {Header: 'Họ tên', accessor: 'hoten', width: 143},
            {Header: 'Ngày mắc bệnh', accessor: 'ngaymacbenh', width: 115},
            {Header: 'Ngày báo cáo', accessor: 'ngaybaocao', width: 115},
            {Header: 'Chuẩn đoán', accessor: 'chuandoan'},
        ],
        data: cabenhs
    }

    return (
        <SidebarLayout
            title={'Chi tiết ổ dịch'}
            icon={`icon-power3`}
            back={true}
            subTitle={<SubTitle items={items}/>}
            content={
                <div>
                    {isEmpty(cabenhs) ? <Alert severity="warning">Không tìm thấy ca bệnh</Alert> : (
                        <>
                            <Slider/>
                            <Tabs activeKey={key} onSelect={k => setKey(k)} className="nav-tabs-bottom nav-justified">
                                <Tab eventKey={TAB_CABENH} title="Ca bệnh">
                                    <TabCabenh tableData={tableData}/>
                                </Tab>
                                <Tab eventKey={TAB_TO_AH} title="Tổ ảnh hưởng">
                                    {key === TAB_TO_AH && <TabToAh models={cabenhs}/>}
                                </Tab>
                                <Tab eventKey={TAB_DNC} title="Điểm nguy cơ">
                                    {key === TAB_DNC && <TabDnc models={cabenhs}/>}
                                </Tab>
                            </Tabs>
                        </>
                    )}

                </div>
            }
        >
        </SidebarLayout>
    )
}

export default memo(OdichViewPage)