import React, { memo } from 'react'
import { useFetch } from '@ttungbmt/react-hooks'
import { ProgressBar } from 'react-bootstrap'
import { InnerHTML } from "@core/components"
import { map } from '@ttungbmt/utils'

function TabToAh({models}) {
    const ids = map(models, 'gid')
    const {isLoading, data} = useFetch({
        url: '/api/odich/to-ah',
        method: 'POST',
        data: {cabenhIds: ids}
    })

    if (isLoading || !data) return <ProgressBar className="m-2" animated now={100}/>

    return <InnerHTML html={data.html}/>
}

export default memo(TabToAh)