import React, { memo } from 'react'
import { useImmerReducer } from 'use-immer'
import { mapValues, get, pick } from '@ttungbmt/utils'
import axios from 'axios'

export const ContainerContext = React.createContext()

const initForm = {
    key: null,
    url: '',
    page: 1,
    values: {},
    data: {},
    filter: '',
    fields: []
}

const initialState = {
    show: false,
    loading: false,
    forms: {

    }
}

const caseReducer = {
    setLoading: (state, payload) => {
        state.loading = payload
    },
    createForm: (state, {key, ...props}) => {
        if(!state.forms[key]) {
            state.forms[key] = {...initForm, key, ...props}
        }
    },
    nextPage: (state, {key, ...props}) => {
        state.forms[key].page = state.forms[key].page + 1
    },
    prevPage: (state, {key, ...props}) => {
        state.forms[key].page = state.forms[key].page === 1 ? 1 : state.forms[key].page -1
    },
    setShow: (state, payload) => {
        state.show = payload
    },
    setFormData: (state, {key, data}) => {
        if(key && data) state.forms[key] = {...state.forms[key], ...data}
    },
    setFormValues: (state, {key, values}) => {
        if(state.forms[key]) {
            state.forms[key].values = values
        } else {
            state.forms[key] = {...initForm, key, values}
        }
    },
    setFormFields: (state, {key, fields}) => {
        state.forms[key].fields = fields
    },
    refreshData: (state, {key}) => {
        state.forms[key].page = 1
        state.forms[key].values = {}
    },
}

const reducer = (draft, action) => caseReducer[action.type] && caseReducer[action.type](draft, action.payload)

function useFetch({actions: acts}) {
    const fetchData = ({form}) => {
        if(form) {
            acts.setLoading(true)
            let params = {
                ...form.values,
                page: form.page,
            }
            axios.get(form.url, {params}).then(({data}) => {
                acts.setFormData({key: form.key, data})
                acts.setLoading(false)
            })
        }
    }

    const fetchColumns = (p) => {
        acts.setLoading(true)
        axios.get(`${p.url}?columns`).then(({data}) => {
            acts.createForm({
                key: p.key,
                url: p.url,
                columns: data.columns,
                filter: data.form
            })
        })
    }

    const fetchForm = ({form, values}, options, callback) => {
        if(form) {
            acts.setLoading(true)
            axios.post(form.url, values).then(({data}) => {
                acts.setFormData({key: form.key, data})
                callback && callback(data)
                acts.setLoading(false)
            })
        }
    }

    const refreshData = (p) => fetchData({form: {
        ...pick(p.form, ['key', 'url']),
        page: 1,
    }})

    return {fetchColumns, fetchData, fetchForm, refreshData}
}

function ContainerProvider({children}) {
    const [state, dispatch] = useImmerReducer(reducer, initialState)
    const actions = mapValues(caseReducer, (v, type) => (payload) => dispatch({type, payload }))
    const fetchFn = useFetch({actions})

    const value = {
        ...state,
        actions: {
            ...actions,
            ...fetchFn,
        },
        getForm: (key) => get(state, `forms.${key}`, {}),
    }

    return (
        <ContainerContext.Provider value={value}>
            {children}
        </ContainerContext.Provider>
    )
}

export default memo(ContainerProvider)