import React, { memo, Fragment } from 'react'
import moment from 'app/utils/moment'
import { RangeSlider } from '@core/components'
import Box from '@material-ui/core/Box'
import { useSelector, useDispatch, useParams } from '@ttungbmt/redux-toolkit'
import { selectors as odichSels, actions as odichActs } from 'app/reducers/odich'
import { debounce } from '@ttungbmt/utils'

const PlayButton = memo(({status, id}) => {
    const dispatch = useDispatch()

    let props = {}
    if (status === 'Stopped') {
        props.classIcon = 'icon-play3'
        props.onClick = () => dispatch(odichActs.runSlider({status, id}))
    } else {
        props.classIcon = 'icon-stop'
        props.onClick = () => dispatch(odichActs.stopSlider({status, id}))
    }

    return <button className="outline-0" onClick={props.onClick}><i className={props.classIcon}/></button>
})


function Slider() {
    const {id} = useParams()
    const dispatch = useDispatch()
    const {range, runStatus} = useSelector(state => odichSels.getOdich(state, id) || {})
    if (!range) return <Fragment/>

    const onChangeSlider = debounce(({from, to}) => {
        dispatch(odichActs.updateRangeValue({id, value: [from, to]}))
    }, 100)

    return (
        <Box display="flex" p={1} bgcolor="background.paper">
            <Box alignSelf="center" className="mr-3">
                <PlayButton id={id} status={runStatus}/>
            </Box>

            <Box flexGrow={1} style={{paddingRight: 20}}>
                {range.minMax.length === 2 &&
                <RangeSlider
                    type="double"
                    min={range.minMax[0]}
                    max={range.minMax[1]}
                    from={range.value[0]}
                    to={range.value[1]}
                    onChange={onChangeSlider}
                    prettify={num => moment(num, 'X').format('L')} grid={true}/>}
            </Box>

        </Box>
    )
}

export default memo(Slider)