import {map, get} from '@ttungbmt/utils'
import $ from 'jquery'

export {default as Field} from './Field'
// export {default as FormProvider, FormContext} from './FormProvider'

export const setFormValues = (values, {control, register, setValue}) => {
    map(values, (v, k) => {
        if(!get(control, `fieldsRef.current.${k}.ref`)) register(k)
        setValue(k, v)
    })
}

export const submitForm = (selector = '#btn-submit-form') => $(selector).trigger('click')
