import React, { memo } from 'react'
import BtnTool from "./BtnTool"
import { Tooltip } from '@core/components'

function ToolGroup({items}) {
    return items.map(({className, ...props}, k) => (
        <div className={className} key={k}>
            <BtnTool {...props}/>
        </div>
    ))
}

export default memo(ToolGroup)