import React, { memo, Fragment } from 'react'

function Body({rows, getTableBodyProps, prepareRow, visibleColumns, renderSubRow, onRowClick, ...props}) {
    return (
        <tbody {...getTableBodyProps()}>
        {rows.map((row, i) => {
            prepareRow(row)
            return (
                <Fragment key={row.getRowProps().key}>
                    <tr onClick={(e) => {
                        e.stopPropagation()
                        onRowClick && onRowClick(e, row)
                    }}>
                        {row.cells.map(cell => {
                            return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                        })}
                    </tr>
                    {row.isExpanded ? (
                        <tr>
                            <td colSpan={visibleColumns.length}>{renderSubRow ?  renderSubRow(row): ''}</td>
                        </tr>
                    ) : null}
                </Fragment>
            )
        })}
        </tbody>
    )
}

export default memo(Body)
