import React from 'react'
import { map, isArray } from '@ttungbmt/utils'
import { uniqid, isUndefined, isString, cloneDeep } from '@ttungbmt/utils'
import cx from "classnames";

export const parseCols = (columns) => {
    return isArray(columns) ? map(columns, ({id, as, value, ...props}, k) => {
        let newProps = cloneDeep(props)

        newProps.Header = props.label ? props.label : () => null

        if(as === 'expand'){
            newProps.Cell = ({row}) => (
                <span {...row.getToggleRowExpandedProps()}>
                    <i className={cx({'icon-diff-added': !row.isExpanded, 'icon-diff-removed': row.isExpanded})}/>
            </span>
            )
        }

        if(as === 'serial'){
            newProps.Header = '#'
            newProps.accessor = props.accessor ? props.accessor : (d, index) => index + 1
            newProps.style = props.style ? props.style : ({minWidth: 57})
        }

        if(value) newProps.accessor = value

        return {
            id: isUndefined(newProps.id) || isUndefined(newProps.accessor) ? uniqid() : id,
            ...newProps,
        }
    }) : columns
}