import React, { memo, useContext } from 'react'
import { useDidMount, useDidUpdate } from '@ttungbmt/react-hooks'
import { Modal, ButtonGroup, Button } from 'react-bootstrap'
import $ from "jquery"
import { keys, isEmpty } from '@ttungbmt/utils'
import { InnerHTML } from "@core/components"

import { submitForm, setFormValues } from '@core/components/Form'
import { ContainerContext } from "../ContainerProvider"

function FormModal({html, name: key, form, methods}) {
    const {show, actions: acts} = useContext(ContainerContext)
    const getForm = () => $('#filterForm').find('form')

    useDidMount(() => {
        form.fields && form.fields.map(f => methods.register(f))
    })

    useDidUpdate(() => {
        if (show && isEmpty(form.fields)) {
            acts.setFormFields({key, fields: keys(getForm().inputValues())})
        }
    }, [show])

    const onEntered = () => {
        let values = methods.getValues()
        getForm().inputValues(values)
        $('#drop-phuong1').on('depdrop:afterChange', function (event, id, value, jqXHR, textStatus) {
            getForm().inputValues({maphuong: values.maphuong})
        })
    }

    const handleFilter = () => {
        setFormValues(getForm().inputValues(), methods)
        submitForm()
        acts.setShow(false)
    }

    const handleReset = () => {
        let $form = $('#filterForm').find('form').get(0)
        $form && $form.reset()
        methods.reset()
        submitForm()
        acts.setShow(false)
    }

    return (
        <Modal id="filterForm" show={show} onHide={() => acts.setShow(false)} onEntered={onEntered}>
            <Modal.Header closeButton
                          style={{padding: '10px 10px', borderBottom: '1px solid #ddd'}}>
                <Modal.Title className="font-weight-semibold" style={{fontSize: 17}}><i
                    className="icon-filter4 mr-2"/> Bộ lọc</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <InnerHTML html={html}/>
            </Modal.Body>
            <Modal.Footer>
                <ButtonGroup>
                    <Button variant="light" onClick={handleReset} size="sm">
                        Xóa
                    </Button>
                    <Button variant="primary" onClick={handleFilter} size="sm">
                        Lọc
                    </Button>
                </ButtonGroup>
            </Modal.Footer>
        </Modal>
    )
}

export default memo(FormModal)