import React, { memo, useEffect, useRef } from 'react'
import PropTypes from 'prop-types';
import { Table as BsTable } from 'react-bootstrap'
import { useTable, useExpanded, useRowSelect } from 'react-table'
import $ from 'jquery'

import { parseCols } from './helpers'
import Header from './Header'
import Body from './Body'


function ReactTable({responsive = 'sm', columns, data, defaultColumn, style, ...props}) {
    const ref = useRef()
    const propTB = useTable({
        columns: parseCols(columns),
        data,
        defaultColumn
    }, useExpanded, useRowSelect)

    useEffect(() => {
        // console.log(ref.current, $(ref.current));
    }, [])

    return (
        <BsTable ref={ref} responsive={responsive} className="table-basic table table-bordered" style={style}>
            <Header {...propTB}/>
            <Body
                {...props}
                {...propTB}
            />
        </BsTable>

    )
}
ReactTable.defaultProps = {
    columns: []
}

ReactTable.propTypes = {
    columns: PropTypes.array,
    data: PropTypes.array,
};

export default memo(ReactTable)
